.nav-bar{
    display: grid;
    grid-template-columns: 1fr 3fr 1fr;
    min-height: 100px;
    background-color: var(--darkgrey);
    width: 100%;
    margin: 0 auto;
    -webkit-box-align:center;
        -ms-flex-align:center;
            align-items:center;
}
.container-scope{
    width: 95%;
