    color: var(--darkgrey);
}
.start{
    color: var(--darkgrey);
}
.middle{
    background-color: var(--lightgrey);
    padding-bottom: 3%;
}
.end{
    background-color: white;
}
.start-flex{
    display: grid;
    grid-template-columns: 1fr 1fr;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    grid-gap: 4em 4em;
    padding: 5% 0;
}
.start-flex-left{
    padding: 2rem 2rem 0 3rem ;
}
.start-flex-left > a{
    margin-right: 3%;
}
.start-flex-left > a:last-child{
    margin-right: 0%;
}
.start-flex-right{
    position: absolute;
    right: 0;  
    min-width: 45%;
    top: 10em;
}
.middle-flex{
    display: grid;
    grid-template-columns: 2fr 2fr 2fr;
    grid-template-rows: 1fr 2.5fr 1fr;
    grid-template-areas: "top top top"
                          "left center right"
                          "bottom bottom bottom";
    grid-gap: 2rem 4rem;
    padding: 0 0 1em 0;
    justify-items: stretch;
}
.middle-flex > h2{
    padding: 2px;
}
.middle-flex-top{
    grid-area: top;
    -ms-flex-item-align: center;
        align-self: center;
    justify-self: center;
}
.middle-flex-top h2{
    padding: 5% 0;
}
.middle-flex-left{
    grid-area: left;
}
.middle-flex-center{
    grid-area: center;
}
.middle-flex-right{
    grid-area: right;
}
.middle-flex-bottom{
    grid-area: bottom;
    justify-self: center;
    -ms-flex-item-align: end;
        align-self: end;
}
.middle-flex-bottom > h3{
    font-weight: 400;
}
.middle-flex-left > h2{
    padding-top: 5%;
    font-weight: 300;
    font-size: 30px;
}
.middle-flex-center > h2{
    padding-top: 5%;
    font-weight: 300;
    font-size: 30px;
}
.middle-flex-right > h2{
    padding-top: 5%;
    font-weight: 300;
    font-size: 30px;
}
.bottom-flex{
    display: grid;
    grid-template-rows: 1fr, 1fr, 1fr, 1fr;
    padding: 2% 0 0 0;
}
.bottom-flex-top{
    justify-self: center;
    line-height: 5em;
    width:25%;
    text-align: center;
    padding-bottom: 3%;
}
.bottom-flex-top h3{
    font-weight: 400;
}
.bottom-flex-center{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    justify-items: center;
    padding: 3% 0;
}
.bottom-flex-bottom{
    padding-top: 3%;
    justify-self: center;
    -ms-flex-item-align: end;
        align-self: end;
}
